import React, { FC } from "react";
import classes from "./Contact.module.scss";
import Container from "../common/Container/Container";
import Title from "../common/Title/Title";
import { TextField } from "@mui/material";

const Contact: FC<{}> = () => {
  return (
    <div className={`container ${classes["container-card"]}`}>
      <div className={classes.background}>
        <div className={`wrapper ${classes["contact-wrapper"]}`}>
          <span>
            <Title title={"Contact"} />
          </span>
          <div className={classes["form-control"]}>
            <TextField fullWidth label={"Name"} InputProps={{ sx: { borderRadius: '20px' } }} />
            <TextField fullWidth label={"E-mail"} InputProps={{ sx: { borderRadius: '20px' } }}/>
            <TextField
              fullWidth
                multiline
              id="outlined-basic"
              label={"Message"}
              variant="outlined"
              rows={10}
              InputProps={{ sx: { borderRadius: '20px' } }}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

export default Contact;
